ul,li{
    margin: 0;
    padding: 0;
    list-style-type: none;
}
#imgList{
    width: 700px;
    overflow: hidden;
    zoom: 1;
    border: 1px solid 3333;
    margin: 0 auto;
    padding: 0 0 10px 10px;
}
#imgList li{
    float: left;
    width: 128px;
    height: 128px;
    border: 1px solid #ccc;
    margin: 10px 10px 0 0;
}
#imgList li img{
    float: left;
    opacity: 0.3;
    cursor: crosshair;
    filter: alpha(opacity=30);
}
#imgList li.current img{
    opacity: 1;
    filter: alpha(opacity=1000);
}